import React from "react";
import { useState, useEffect } from "react";
import "./styles.css";

function App() {
  const [todo, setTodo] = React.useState("");
  const [todo1, setTodo1] = React.useState("");
  const [todo2, setTodo2] = React.useState("");
  const [todo3, setTodo3] = React.useState("");

  const [id, setId] = React.useState("");

  React.useEffect(() => {
    if (id == null || id === "") {
      return;
    }

    fetch(`https://db.ygoprodeck.com/api/v7/cardinfo.php?name=${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo(data.data[0]);
      });
    fetch(`https://db.ygoprodeck.com/api/v7/cardinfo.php?name=${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo1(data.data[0].card_images[0].image_url);
      });
    fetch(`https://db.ygoprodeck.com/api/v7/cardinfo.php?name=${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo2(data.data[0].card_images[1].image_url);
      });

    fetch(`https://db.ygoprodeck.com/api/v7/cardinfo.php?name=${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo3(data.data[0].card_images[2].image_url);
      });
  }, [id]);

  return (
    <>
      <h1>Yu-Gi-Oh! Card Picture App made by Fernando </h1>
      <p>
        Some cards to check out in box are
        <li>Obelisk The Tormentor</li>
        <li>Kuriboh</li>
        <li>Dark Magician Girl </li>
        <li>Red-Eyes Black Dragon</li>
        <li>Slifer The Sky Dragon</li>
        <li>Exodia The Forbidden One</li>
      </p>
      <div>
        <input value={id} onChange={(e) => setId(e.target.value)} />
        <br />
        <img src={todo1} width="500" px height="700" px />
        <img src={todo2} width="500" px height="700" px />
        <img src={todo3} width="500" px height="700" px />

        <pre>{JSON.stringify(todo, null, 2)}</pre>
      </div>
    </>
  );
}

export default App;
